import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { setFormatPrice } from "../../FORMAT/price";

export const Paragraph = (typePlan, indexPlan) => {

    const [price, setPrice] = useState(0);
    const { allPackageData } = useSelector(state => state.planState);
    // const price = allPackageData.lenght > 0 ? allPackageData[indexPlan].default_discount:0;
    useEffect(() => {
        // debugger
        if (allPackageData.length > 0) {
            var packPrice;
            // debugger
            if (indexPlan==4|indexPlan==8|indexPlan==12) {
                packPrice = allPackageData[indexPlan+4].default_discount
            }else{
                packPrice = allPackageData[indexPlan != 16 ? indexPlan+1 : indexPlan].default_discount
            }
            // console.log(Math.ceil(packPrice))
            
            setPrice(setFormatPrice(Math.ceil(packPrice)))
        }
    }, [indexPlan])


    switch (indexPlan) {
        //  
        case 2:
            return `  6       	    ${price}   .`
        case 6:
            return `  6       	    ${price}   .`
        case 10:
            return `  6       	    ${price}   .`
        case 14:
            return `  6       	    ${price}   .`


        //  
        case 3:
            return `  12       	2    ${price}   .`
        case 7:
            return `  12       	2    ${price}   .`
        case 11:
            return `  12       	2    ${price}   .`
        case 15:
            return `  12       	2    ${price}   .`



        //  
        case 4:
            return `  12         	2    ${price}   .`
        case 8:
            return `  12        	2    ${price}   .`
        case 12:
            return `  12        	2    ${price}   .`
            // TODO:               12 
        case 16:
            return `         .`
        default:
            break;
    }

    switch (typePlan) {
        case "bronze":
            return `  3        15%   ${price}   .`
        case "silver":
            return `  3        15%   ${price}   .`
        case "gold":
            return `  3        15%   ${price}   .`
        case "diamond":
            return `  3        15%   ${price}   .`
        default:
            break;
    }

    // return "   12      10     2    "
    return ""
}