export const paragraphText = (step, operationType) => {
    switch (step) {
        case 1:

            return (
                <p className='text-primaryV1 text-sm px-5'>
                    {operationType == "verify" ? (
                        "             :"
                    ) : (
                        "           :"
                    )}
                </p>
            )
        case 2:
            return (
                <p className='text-primaryV1 text-sm px-5'>
                      <span className="text-primaryV1 border-b"></span>           .
                    <br/>
                    <br/>
                              .
                </p>
            )

    default:
      break;
  }
};
