export const TitleModal = (stepModal, free) => {
  switch (stepModal) {

    case 1:
      return " ";
    case 2:
      if (free) {
        return "14  ";
      }
      return "";
    case 3:
      return " ";
    case 4:
      return "     ";
    case 5:
      return "  ";
    case 6:
      return "   ";
    default:
      break;
  }

}